// BUTTONS
// ---------------------------------------------------------------------------------------------------------------------
@import "bjb-variables.scss";

.btn {
  display: inline-block;
  padding: 10px 25px;

  font-family: $bjb-fontfamily-bold;
  font-size: $font-size-13;
  font-weight: normal;
  line-height: 1;
  text-decoration: none;
  outline: none !important;

  border-radius: 0px;

  &:active {
    background-color: $bjb-white !important;
    border: 2px solid $bjb-blue !important;
  }

  &[disabled="disabled"],
  &.disabled {
    background-color: $bjb-black-20 !important;
    border: 1px solid $bjb-black-20 !important;
  }

  &.btn-primary {
    font-family: $bjb-font-bold;
    background-color: $bjb-blue;
    border-color: $bjb-blue;
    color: $bjb-white;
  }

  &.btn-secondary {
    font-family: $bjb-font-bold;
    background-color: $bjb-white;
    border-color: $bjb-black-20;
    color: $bjb-black;
  }
}


.btn-default,
.btn-primary {

  &:hover {
    background-color: $bjb-white;
    color: $bjb-blue;
    border: 1px solid $bjb-blue;
    text-decoration: none !important;
  }

  &:active,
  &.active {
    background-color: $bjb-white !important;
    color: $bjb-blue !important;
    border: 2px solid $bjb-blue !important;
    padding: 7px 24px !important;
  }
}

.btn-secondary {

  &:hover {
    background-color: $bjb-blue-20;
    color: $bjb-white;
    // border: 1px solid $bjb-blue;
    text-decoration: none !important;
  }

  &:active,
  &.active {
    background-color: $bjb-blue !important;
    color: $bjb-white !important;
    border: 2px solid $bjb-blue !important;
    padding: 7px 24px !important;
  }
}

// LINKS
// ---------------------------------------------------------------------------------------------------------------------

a,
a:link,
a:active,
a:visited {
  font-family: $bjb-font-light;
  //font-size: $font-size-13;
  color: $bjb-blue;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

a.bjb-link,
a.bjb-link:link,
a.bjb-link:active,
a.bjb-link:visited {
  color: $bjb-blue;
  font-family: $bjb-font-bold;
  //font-size: $font-size-13;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

a.bjb-link-arrow,
a.bjb-link-arrow:link,
a.bjb-link-arrow:active,
a.bjb-link-arrow:visited {
  color: $bjb-blue;
  font-family: $bjb-font-light;
  //font-size: $font-size-13;
  text-decoration: none;

  &:hover {
    color: $bjb-blue;
    text-decoration: underline;
  }

  &::before {
    content: "> ";
  }
}

