$colors: #001489 #3343A1 #6672B8 #99A1D0 #CCD0E7 #B0AA7E #C0BB98 #D0CCB2 #DFDDCB #EFEEE5;

/***************************************************************************
 STYLES FOR ALL CHARTS
****************************************************************************/

// Fonts
$font-family: $bjb-fontfamily-normal;
$font-family-bold: $bjb-fontfamily-bold;
$title-font-size: $font-size-16;
$subtitle-font-size: $font-size-12;
$legend-font-size: $font-size-11;
$axis-labels-font-size: $font-size-11;

.highcharts-title {
    font-family: $font-family-bold;
}

.highcharts-subtitle {
    fill: $gray-base !important;
}

// already disabled in Highcharts.options
.highcharts-credits {
    display: none;
}

.highcharts-axis-labels {
    fill: $gray-base !important;
    font-size: $legend-font-size !important;
}

.highcharts-point {
    stroke-width: 0 !important;
}

.highcharts-data-label {
    text {
        font-weight: normal !important;
    }
}

.highcharts-legend-item {
    display: inline-block;
    margin-bottom: 5px;

    text {
        font-weight: normal !important;
    }
}

/***************************************************************************
 CHART SPECIFIC STYLES
****************************************************************************/
.bjb-chart-pie {
    .highcharts-series {
        .highcharts-point {
            stroke-width: 1px !important;
        }
    }
}

.bjb-column-chart-single-colour-in-sequential-order {
    $colors-column-chart-single-color-in-sequential-order: #001489 #6672B8 #CCD0E7;
    @for $i from 1 through length($colors-column-chart-single-color-in-sequential-order) {
        $color: nth($colors-column-chart-single-color-in-sequential-order, $i);
        .highcharts-color-#{$i - 1} {
          fill: $color;
          stroke: $color;
        }
    }
}

