  url('#{$icomoon-font-path}/bjb-icons.ttf?vs2g5t') format('truetype'),
  url('#{$icomoon-font-path}/bjb-icons.woff?vs2g5t') format('woff'),
  url('#{$icomoon-font-path}/bjb-icons.svg?vs2g5t#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon_"], [class*=" icon_"] {
