@import "bjb-variables.scss";

/* these are not regular variables but css browser instructions to load fonts, 
   they should be kept seperate from regular less / css code */

@font-face {
	font-family: 'Verlag SSm 3r';
	src: url($bjb-font-path + '5C5449B44C47E9E6A.eot');
	src: local('?'),
		url($bjb-font-path + '5C5449B44C47E9E6A.eot?#hco') format('embedded-opentype'),
		url(data:application/x-font-woff;base64,),
		url(data:font/truetype;base64,);
	font-weight: normal;
	font-style: normal;
}

