body {

  padding-bottom: 0px;

  &.bjb-sp {
    padding-bottom: 0px;

    img {
      max-width: initial;
      width: auto;
    }

    .bjb-login-strip {
      display: block !important;
    }

    #suiteBar {
      display: block;
    }

    #suiteBarLeft {
      display: inline-block !important;

      p {
        margin: 0;
      }
    }

    #suiteBarLeft,
    #suiteBarRight {
      background-color: transparent;
    }

    .ms-cui-dd,
    .ms-cui-cp {

      *,
      &::after,
      &::before {
        box-sizing: content-box;
      }
    }

    #siteactiontd {
      width: 30px;
    }

    #globalNavBox {
      background-color: transparent;

      .ms-cui-topBar2 {
        border-bottom: 0px solid transparent;
      }
    }

    #RibbonContainer-TabRowRight {
      display: none;
    }

    .ms-core-menu-buttonIcon {
      width: auto;
      max-width: none;
    }

    .ms-core-menu-box,
    .ms-core-menu-boxBig {
      background-color: #141E55;
    }
    
    .ms-core-menu-box a:hover {
      background: #727899;
    }

    .ms-core-menu-box.ms-core-defaultFont.ms-shadow {
      width: 200px !important;
    }

    .ms-core-menu-root {
      line-height: normal !important;
      width: 30px !important;
      height: 30px !important;
    }

    .ms-core-menu-list {
      max-height: none;
      overflow-x: none;

      a {
        font-size: 13px;
      }
    }

    #s4-workspace {
      background-color: transparent;
    }

    #MenuH > div {
      text-align: center;
    }

    /* Styleguide overwrites */

    .bjb-content {
      padding: 0;
    }

    .bjb-footer > {

      div {
        display: flex;
      }

      .bootstrap-footer {
        max-width: 1140px;
        margin: 0 auto;
      }
    }

    #MSOTlPn_MainTD {

      img {
        max-width: none;
        width: auto;
      }

      label {
        margin-bottom: 0px;
        padding-left: 5px;
      }
    }

    .ms-webpart-menuArrowImg {
      width: auto;
      max-width: none;
    }

    #s4-ribbonrow {

      a {
        font-size: 11px;

        &:link,
        &:active,
        &:visited {
          font-size: 11px;
        }
      }

      img {
        max-width: none;
        width: auto;
      }
    }

    .bjb-topic-box.bjb-layout-boxes h3 {
      float: none;
    }

    .bjb-rss-box.bjb-layout-boxes {
      max-width: none;
      padding: 0;
    }

    /* Main Container Shadow */

    #s4-bodyContainer {
      box-shadow: inset 0px 15px 15px -10px rgba(0, 0, 0, 0.2), inset 0px -15px 15px -10px rgba(0, 0, 0, 0.2);
    }

    #menuOuter {
      padding-left: 0;

      & > li {
        list-style-type: none;
      }
    }

    /* SharePoint Standard Elemente berschreibungen */
    .s4-wpcell > .ms-webpart-chrome,
    .s4-wpActive > .ms-webpart-chrome {
      background-color: white;
    }

    .ms-webpart-titleText {
      padding-bottom: 15px;
      border-bottom: 1px solid #99A1D0;
      font-size: 19px;
      margin: 0 15px;
      padding-top: 15px;

      & > a {
        font-size: 19px;
      }
    }

    .ms-webpart-zone {
      padding: 0 15px;
    }

    .ms-webpartzone-cell {
      box-shadow: 0px 30px 20px -25px #bbb;
    }

    #contentRow {
      width: 100%;
    }

    .ms-summaryLinksToolbar > a {
      font-size: 11px;

      & > img {
        width: 16px;
      }
    }

    .ms-webpart-menuLink {
      width: 13px;
      height: 13px;
    }

    .ms-welcome-hover {
      background: transparent !important;
      color: transparent !important;
      border-right-color: transparent !important;
      
      & > a {
        color: #444 !important;
      }
    }

    .bjb-login-strip a:hover,
    .ms-siteactions-hover {
      color: #FFFFFF !important;
      background-color: $bjb-royal-blue !important;
    }

    .bjb-nav-sub {
      ul {
        li {
            a {
              &:hover {
                background-color: #FFFFFF !important;
                color: $bjb-royal-blue !important;
              }
            }
          ul {
            li {
              a {
                &:hover {
                  background-color: #FFFFFF !important;
                  color: $bjb-royal-blue !important;
                }
              }
              ul {
                li {
                  a {
                    &:hover {
                      background-color: #FFFFFF !important;
                      color: $bjb-royal-blue !important;
                    }
                  }
                  ul {
                    li {
                      a {
                        &:hover {
                          background-color: #FFFFFF !important;
                          color: $bjb-royal-blue !important;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    li.dynamic {
      list-style-type: none;
    }

    #contentBox {
      margin: 0;
    }

    .intranet_footer_table_right,
    .intranet_footer_printpage,
    .intranet_footer_link_top {
      & > a > img  {
        width: 16px;
      }
    }

    .ms-dlgTitle .ms-dlgTitleBtns span {
      box-sizing: content-box;
    }

    #siteactiontd {
      width: 30px;
    }

    #suiteBar {
      display: block;
    }

    #suiteBarLeft,
    #suiteBarRight {
      background-color: transparent;
    }

    #globalNavBox {
      background-color: transparent;
    }

    #globalNavBox .ms-cui-topBar2 {
      border-bottom: 0px solid transparent;
    }

    #RibbonContainer-TabRowRight {
      display: none;
    }

    .s4-clust img {
      max-width: none !important;
    }
    
    .ms-core-menu-link > .ms-core-menu-label > span.ms-core-menu-title {
        color: #FFFFFF !important;
    }
    
    .ms-cui-ctl-iconContainer span img {
        max-width: none !important;
    }
    
    .ms-core-menu-root:hover {
        color: #FFFFFF !important;
    }
    
    .ms-menu-althov > a {
        color: #FFFFFF !important;
    }
    
    .ms-menu-althov-active a:hover,
    .ms-welcome-root a:hover,
    .ms-welcome-hover a:hover {
        color: #FFFFFF !important;
    }
    
    .ms-welcome-hover>a.ms-core-menu-root {
        color: #FFFFFF !important;
    }

  }
}