// ---------------------------------------------------------------------------------------------------------------------
// GLOBALS FROM OTHERS.LESS
// ---------------------------------------------------------------------------------------------------------------------
@import "bjb-variables.scss";


// ---------------------------------------------------------------------------------------------------------------------
// BOOTSTRAP REWRITES
// ---------------------------------------------------------------------------------------------------------------------

.form-control {
  line-height: 165%;
  height: 32px;
  color: $bjb-green-smoke;
}

.container {
  background-attachment: scroll;
  background-repeat: repeat;
  background-position-x: 0;
  background-position-y: 0;
  background-size: auto;
  background-origin: padding-box;
  background-clip: border-box;
}



// ---------------------------------------------------------------------------------------------------------------------
// GLOBALS
// ---------------------------------------------------------------------------------------------------------------------

body {
  background-color: $bjb-body-bgcolor;
  padding-bottom: 20px;
  overflow-x: hidden;

  font-family: $bjb-font-light !important;
  font-size: $font-size-14;
  line-height: 1.4;
}

//==Typography
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: $bjb-font-light;
  font-weight: normal;
  text-transform: uppercase;
  color: $bjb-blue;
}


h1,
.h1 {
  font-size: $font-size-h1;
}

h2,
.h2 {
  font-size: $font-size-h2;
}

h3,
.h3 {
  font-size: $font-size-h3;
}

h4,
.h4 {
  font-size: $font-size-h4;
}

h5,
.h5 {
  font-size: $font-size-h5;
}

h6,
.h6 {
  font-size: $font-size-h6;
}

p {
  font-size: $font-size-14;
}

strong {
  font-family: $bjb-fontfamily-bold;
  line-height: 130%;
}

//==Labels
label {
  font-family: $bjb-font-bold;
  font-weight: normal;
}

.radio, .checkbox {
  line-height: 165%;
}



// BORDER LINES
// ---------------------------------------------------------------------------------------------------------------------

.border-bottom {
  border-bottom: 1px solid $bjb-blue-40;
}

.border-top {
  border-top: 1px solid $bjb-blue-40;
}

.bjb-border-blue {
  border-color: $bjb-blue-40 !important;
}

.bjb-border-black {
  border-color: $bjb-black-20 !important;
}

.bjb-color-blue {
  color: $bjb-blue;
}

.bjb-color-black {
  color: $gray;
}

.bjb-divider-blue {
  width: 100%;
  height: 1px;
  background-color: $bjb-blue-40;
  margin: 15px 0;
}

.bjb-divider-black {
  width: 100%;
  height: 1px;
  background-color: $bjb-black-20;
  margin: 15px 0;
}

.list-style-type-none {
  list-style-type: none;
  padding: 0;
}

// ---------------------------------------------------------------------------------------------------------------------
// HEADER
// ---------------------------------------------------------------------------------------------------------------------

.bjb-header {
  background-color: $bjb-white;
  border-bottom: 1px solid $bjb-black-20;

  &.rectangle-logo-wrapper {
    position: relative;
  }

  &.bjb-header-rectangle-logo {
    background-color: $bjb-royal-blue;
    height: 514px;
    position: relative;
    max-height: 50vh;

    img {
      width: 390px;
      position: absolute;
      top: 50%;
      left: 50%;
      padding: 35px 35px 30px 35px;
      margin-left: -185px;
      margin-top: -64px;
    }
  }

  .bjb-header-lang {
    display: flex;
    margin-top: 30px;
    margin-bottom: 25px;
    align-items: center;

    select {
      border: none;
      //background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='9' height='5' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M.514 0l3.964 4.48L8.438 0' stroke='%23001489' fill='none' fill-rule='evenodd' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
      //background-size: 13px 8px;
      //background-position: 40px 14px;
      background-image: url(../img/icons/icon_select_arrow.svg);
      background-size: 9px 5px;
      background-position: 40px 16px;
    }

    ul.desktop {
      display: flex;
      margin: 0;
      line-height: 1;

      li {
        margin: 0;
        line-height: 1;

        a {
          position: relative;
          font-family: $bjb-font-light;
          font-size: $font-size-14;
          line-height: 1;
          padding: 0;
          margin: 0 15px 0 0;
          text-transform: uppercase;
          text-decoration: none;
          border-bottom: 1px solid transparent;
          
          &:hover,
          &.active {
            &:after {
              content: '';
              position: absolute;
              left: 0; bottom: -7px;
              height: 1px;
              background-color: $bjb-blue;
              width: 100%;
            }
          }
        }

        &:last-child {
          margin: 0;
        }
      }
    }


    ul.mobile {
      margin-bottom: 0;
      padding-left: 0;
      display: none !important;
    }

  }

  .bjb-logo {
    width: 100%;
    margin-top: 30px;
    margin-bottom: 25px;

    img {
      max-width: 150px;
    }
  }

  .bjb-header-util {
    margin-top: 30px;
    margin-bottom: 25px;

    .bjb-help,
    .bjb-navigation {
      display: inline-block;
      min-width: 90px;
      line-height: 30px;
      vertical-align: middle;

      a {
        padding: 0;
        font-family: $bjb-font-light;
        font-size: $font-size-14;
        line-height: 1;
        color: $bjb-blue;

        span {
          display: inline-block;
          position: relative;
          padding: 0 36px 0 0;
          font-family: $bjb-font-light;
          font-size: $font-size-14;
          line-height: 1;
          color: $bjb-blue;

          &:hover,
          &.active {
            &:after {
              content: '';
              position: absolute;
              left: 0; bottom: -7px;
              height: 1px;
              background-color: $bjb-blue;
              width: calc(100% - 36px);
            }
          }

          i:before {
            position: absolute;
            right: -5px;
            top: -9px;
            transform: scale(0.75);
          }
        }

        &[aria-expanded="true"] {
          i:before {
            background-position: -8.8em 0;
          }
        }
      }
    }

    .bjb-navigation {
      display: none;
    }
  }

  h5 {
    line-height: 1;
    color: $bjb-blue;
    margin-bottom: 1rem;
  }

  nav.row {
    padding: 0;

    >div {
      display: block;
      margin: 0 auto;

      >ul {
        justify-content: left !important;

        >li {
          margin: 0;
          position: relative;

          >a,
          >span.nav-link {
            display: inline-block;
            position: relative;
            padding: 0;
            margin: 0 1rem;
            color: $bjb-black !important;
            text-decoration: none !important;

            &:hover,
            &.active {
              color: $bjb-blue !important;
            }

            &.dropdown-toggle::after {
              display: inline-block;
              background-image: url('../../assets/img/icons/icon_nav_dn.svg');
              background-repeat: no-repeat;
              width: 9px;
              height: 6px;
              border: none;
              margin-left: 5px;
              margin-bottom: 1px;
              content: "";
              vertical-align: inherit;
            }

            &[aria-expanded="true"] {
              &.dropdown-toggle::after {
                background-image: url('../../assets/img/icons/icon_nav_up.svg');
              }
            }

            &:hover,
            &.active {
              &:before {
                content: '';
                position: absolute;
                left: 0;
                bottom: -5px;
                height: 1px;
                background-color: $bjb-blue;
                width: 100%;
              }
            }
          }

          >span.nav-link {
            font-size: $font-size-13;

            &:hover {
              text-decoration: none !important;
              color: inherit !important;

              &:before {
                display: none;
              }
            }
          }

          .dropdown-menu {
            border-radius: 0;
            margin-top: 10px;

            .dropdown-item {
              position: relative;
              color: $bjb-black !important;
              padding-left: 1rem;

              &.active,
              &:hover {
                &:before {
                  content: "> ";
                  position: relative;
                  top: 0;
                  left: 0;
                  margin-left: -10px;
                }

                color: $bjb-blue !important;
                background-color: $bjb-white !important;
                text-decoration: none !important;
              }
            }
          }
        }
      }
    }
  }
}

.bjb-header-hidden-nav {

  .bjb-multi-header {

    &.show {
      display: flex !important;
      flex-wrap: wrap;
    }
  }
}


.show {

  &.bjb-multi-header {
    li {
      .nav-link {
        display: none;

        &.nav-desktop {
          font-size: 14px;
          color: $bjb-blue !important;
          text-transform: uppercase;
        }
      }
      .dropdown-menu {
        display: block;
      }
    }
  }

  li {
    .dropdown-menu {
      //display: block;
      border: none;

      .dropdown-item {

      }
    }
  }
}

@media only screen and (max-width : 991px) {
  .bjb-header:not(.fix) {
    z-index: 20;

    .bjb-header-util {
      .bjb-navigation {
        display: inline-block;
      }
    }

    nav {
      position: absolute;
      margin: 0 auto;
      background-color: $bjb-white;
      top: 0;
      left: 0;
      width: 100%;
      text-align: center;
      margin-top: 87px;
      z-index: 100;
      align-items: flex-start;

      >div {
        width: 100%;
      }

      ul {
        padding-top: 50px !important;
        padding-bottom: 50px !important;
        height: 100%;
        margin-bottom: 0;

        &.multi-navigation {
          .list-inline-item {
            max-width: 50%;
          }
        }





        .list-inline-item {
          max-width: 50%;
          margin: 0 auto !important;
          padding: 20px 0;
          font-family: $bjb-font-light;
          font-size: $font-size-14;
          line-height: 1;
          color: $bjb-black;

          &:not(:last-child) {
            border-bottom: 1px solid $bjb-black-20;
          }
        }
      }

      .dropdown {
        .dropdown-toggle::after {
          position: absolute;
          margin-top: 5px;
          margin-bottom: 0;
        }

        &.show {
          .dropdown-toggle {
            color: $bjb-blue !important;
          }
        }
      }

      .dropdown-menu {
        position: relative;
        float: none;
        text-align: center;
        border: none;

        a {
          padding: 10px;

          &.active,
          &:hover {
            &:before {
              content: "> ";
              position: relative;
              top: 0;
              left: 0;
              margin-left: -10px;
            }

            color: $bjb-blue !important;
            background-color: $bjb-white !important;
            text-decoration: none !important;
          }
        }
      }

      .list-inline-item {
        display: block;
      }
    }
  }

  .show.bjb-multi-header li .dropdown-menu {
    display: none;
  }







  .show {
    &.bjb-multi-header {
      li {
        .nav-link {
          display: block;

          &.nav-desktop {
            display: none;
          }
        }
      }
    }
  }

  .bjb-header-hidden-nav:not(.fix){
    ul {
      &.bjb-multi-header {
        &.show {
          display: block !important;
        }

        .list-inline-item {
          margin: 0 auto !important;

          &:not(:last-child){
            border-bottom: 1px solid $bjb-black-20 !important;
          }
        }
      }
    }
  }

}



// ---------------------------------------------------------------------------------------------------------------------
// HEADER WITH HIDDEN NAVIGATION
// ---------------------------------------------------------------------------------------------------------------------

.bjb-header-hidden-nav {
  @extend .bjb-header;

  .bjb-header-util {
    .bjb-navigation {
      display: inline-block;
    }
  }




  .bjb-multi-header {
    display: none !important;

    .list-inline-item {
      text-align: center!important;
    }

    &.show {
      display: flex !important;
    }
  }

  nav>ul:not(.show) {
    display: none !important;
  }
}

@media only screen {
  .bjb-header-hidden-nav:not(.fix) {
    nav {
      position: absolute;
      margin: 0 auto;
      background-color: $bjb-white;
      top: 0;
      left: 0;
      width: 100%;
      text-align: center;
      margin-top: 87px;
      z-index: 100;
      align-items: flex-start;

      ul {
        border-top: none;
        border-bottom: none;
        padding: 50px 0 !important;
        height: 100%;
