        &:not(.show) {
          /*&:not(.multi-navigation){
            display: none !important;
          }*/
        }

        .list-inline-item {
          max-width: 50%;
          margin: 0 !important;
          //padding: 20px !important;
          font-family: $bjb-font-light;
          font-size: $font-size-14;
          line-height: 1;
          color: $bjb-black;
          text-align: left;

          &:not(:last-child) {
            border-bottom: none !important;
          }
        }

        &:not(.multi-navigation) {
/*          .dropdown-toggle::after {
            display: none !important;
          }*/

          .dropdown-menu {
            &.show{
              display: block !important;
              position: relative;
              float: none;
              text-align: center;
              //border: none;

              a {
                &:before {
                  display: none;
                }
              }

            }
          }
        }

      }

      .dropdown {

        &.show {
          .dropdown-toggle {
            color: $bjb-blue !important;
          }
        }
      }



      .list-inline-item {
        display: block;
      }
    }

    nav.row {
      .bjb-navigation-lang {
        >div {
          width: auto !important;

          >ul {
            display: flex;
            flex-wrap: wrap;

            &:not(.multi-navigation) {
              >li {

                .dropdown-menu {
                  min-width: 1px;
                  padding: 0;
                  display: inline-block !important;
                  margin: 10px 0 0 0;
                  width: 100%;

                  &:not(.show){
                    display: none !important;
                  }

                  .dropdown-item {
                    display: block;
                    padding: 5px 0;
                    text-align: left;
                    white-space: nowrap;
                    color: $bjb-black !important;

                    &.active,
                    &:hover {
                      &:before {
                        display: inline;
