                        content: "> ";
                        top: 0;
                        left: 0;
                        margin-left: -10px;
                      }

                      color: $bjb-blue !important;
                      background-color: $bjb-white !important;
                      text-decoration: none !important;
                    }
                  }
                }

                >a {
                  &:after {
                    display: none !important;
                  }
                }
              }

            }

            >li {
              display: inline-block;
              float: left;
              padding: 20px;

              >a {
                display: block;
                margin: 0;
                padding: 0;
                text-transform: uppercase;
                color: $bjb-blue !important;

              }


            }
          }
        }
      }

    }
  }
}



