// ---------------------------------------------------------------------------------------------------------------------
// ICONS
// ---------------------------------------------------------------------------------------------------------------------

// TODO cha styleguide: use this class
.bjb-icon {
  display: inherit;
  background-position: 0 0;
  background-repeat: no-repeat;

  &.pdf {
    display: inline-block;
    background-image: url('../../assets/img/icons/pdf_16x16.png');
    width: 16px;
    height: 16px;

    &.big {
      display: inline-block;
      background-image: url('../../assets/img/icons/pdf_32x32.png');
      width: 32px;
      height: 32px;
    }
  }

  &.placeholder {
    background-image: url('../../assets/img/icons/icon-placeholder.jpg');
    width: 15px;
    height: 15px;
  }

  &.arrow-up::after {
    content: " >";
    -webkit-transform: rotate(-90deg);
    display: inline-block;
    margin-left: 12px;
    vertical-align: middle;
  }

  &.icon_success {
    width: 13px;
    height: 13px;
    background-image: url('../img/icons/icon_validate_white.svg');
    background-size: 13px 13px;

    &:before {
      display: none;
    }
  }
}



// TABLE SORT
// ---------------------------------------------------------------------------------------------------------------------

.table {
  &.bjb-table {
    td {
      padding: 8px;
    }
  }
}

.bjb-sortable {
  position: relative;
  display: inline-block;
  top: 1px;
  left: 3px;
}

.bjb-sortable::after {
  content: '';
  position: absolute;
  bottom: 6px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 3.5px 5px 3.5px;
  border-color: transparent transparent $bjb-blue transparent;
}

.bjb-sortable::before {
  content: '';
  position: absolute;
  bottom: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 3.5px 0 3.5px;
  border-color: $bjb-blue transparent transparent transparent;
}

.bjb-loading {
  &::before {
    content: '';
    width: 16px;
    height: 16px;
    background: url('../../assets/img/icons/icon-loading.gif') no-repeat 0 0;
    margin: 0px 5px 0 -8px !important;
    float: left;
  }
}



// ---------------------------------------------------------------------------------------------------------------------
// ELEMENTS
// ---------------------------------------------------------------------------------------------------------------------

// ELEMENTS BOX 1
// ---------------------------------------------------------------------------------------------------------------------

.bjb-box-gray {
  color: $bjb-blue;
  line-height: 190%;
  background-color: $bjb-box-bg-color-gray;
  padding: $bjb-padding;
}



// ELEMENTS BOX 2
// ---------------------------------------------------------------------------------------------------------------------

.bjb-box-blue {
  color: $bjb-white;
  line-height: 190%;
  background-color: $bjb-royal-blue;
  padding: $bjb-padding;
}



// ELEMENTS IMAGE
// ---------------------------------------------------------------------------------------------------------------------

.bjb-image-box {
  .figure-img {
    max-width: initial;
    width: 100%;
  }

  .figure-caption {
    font-size: $font-size-12;
    color: $bjb-black;
  }
}

.bjb-image-box-nopadding {
  padding: 0 !important;
}



// ELEMENTS MULTI COLUMNS
// ---------------------------------------------------------------------------------------------------------------------

.bjb-multicolumn {
  display: inline-block;

  .column-image {
    @extend .bjb-image-box;
  }

  .column-text {

    h4 {
      color: $bjb-blue;
    }

    p {
      color: $bjb-black;
      padding: 0;
      margin: 20px 0;
    }

    a {
      width: 100%;
      display: block;
    }
  }
}



// TABLES
// ---------------------------------------------------------------------------------------------------------------------

table, .table {
  thead {
    th {
      border-top: none;
      font-size: $font-size-13;
      padding: 5px;
    }
  }

  tbody {
    border-bottom: 1px solid transparentize($bjb-black-20, .5);
  }

  td {
    border-bottom: 1px solid transparentize($bjb-black-20, .5);
    font-size: $font-size-12;
    padding: 5px;
  }

  th {
    color: $bjb-black;
    border-bottom-color: $bjb-black-40 !important;
    border-bottom-width: 1px !important;
    font-family: $bjb-font-bold;
    font-weight: normal;
  }

  td {
    color: $bjb-black;
  }

  &.table-striped {
    tbody {
      tr:nth-of-type(odd) {
        background-color: $bjb-stone-10;
      }
    }
  }

  &.table-bordered {
    thead {
      border: 1px solid $bjb-blue-40;

      th:first-child {
        border-left: 1px solid $bjb-blue-40;
      }
      th:last-child {
        border-right: 1px solid $bjb-blue-40;
      }
    }

    tbody {
      td:first-child {
        border-left: 1px solid $bjb-blue-40;
      }
      td:last-child {
        border-right: 1px solid $bjb-blue-40;
      }
      tr:last-child {
        td {
          border-bottom: 1px solid $bjb-blue-40;
        }
      }
    }
  }
}

img {
  max-width: 100%;
  width: 100%;
}



// ELEMENTS LIST BULLETS
// ---------------------------------------------------------------------------------------------------------------------

div.bjb-list-bullets-lines {
  h5 {
    color: $bjb-blue;
    line-height: 1;
    margin: 0 0 8px 8px;
  }

  ul {
    @extend .border-top;
    margin: 0;
    padding: 0;
    list-style: none;

    li {
      position: relative;
      font-family: $bjb-font-light;
      font-size: $font-size-12;
      color: $bjb-black;
      border-color: $bjb-black-20;
      margin: 0;
      padding: 8px 0;

      &:before {
        content: "\2022\00a0\00a0";
        margin-left: 8px;
      }

      &:after {
        position: absolute;
        display: block;
        content: "";
        width: 100%;
        height: 1px;
        background-color: $bjb-black-20;
        margin-top: 8px;
      }
    }
  }
}



div.bjb-list-bullets {
  h5 {
    color: $bjb-blue;
    line-height: 1;
    margin: 0 0 8px 8px;
  }

  ul {
    @extend .border-top;
    @extend .border-bottom;
    margin: 0;
    padding: 0;
    list-style: none;

    li {
      position: relative;
      font-family: $bjb-font-light;
      font-size: $font-size-12;
      color: $bjb-black;
      border-color: $bjb-black-20;
      margin: 8px 0;
      padding: 0;

      &:before {
        content: "\2022\00a0\00a0";
        margin-left: 8px;
      }
    }
  }
}



// ELEMENTS LIST TEXT
// ---------------------------------------------------------------------------------------------------------------------

div.bjb-list-text-lines {
  h5 {
    color: $bjb-blue;
    line-height: 1;
    margin: 0 0 8px 8px;
  }

  ul {
    @extend .border-top;
    margin: 0;
    padding: 0;
    list-style: none;

    li {
      position: relative;
      font-family: $bjb-font-light;
      font-size: $font-size-12;
      color: $bjb-black;
      border-color: $bjb-black-20;
      margin: 0;
      padding: 8px 0;

      &:before {
        content: "";
        margin-left: 8px;
      }

      &:after {
        position: absolute;
        display: block;
        content: "";
        width: 100%;
        height: 1px;
        background-color: $bjb-black-20;
        margin-top: 8px;
      }

      strong {
        &:after {
          content: "";
          margin-right: 16px;
        }
      }
    }
  }
}

div.bjb-list-text {
  h5 {
    color: $bjb-blue;
    line-height: 1;
    margin: 0 0 8px 8px;
  }

  ul {
    @extend .border-top;
    @extend .border-bottom;
    margin: 0;
    padding: 0;
    list-style: none;

    li {
      position: relative;
      font-family: $bjb-font-light;
      font-size: $font-size-12;
      color: $bjb-black;
      border-color: $bjb-black-20;
      margin: 8px 0;
      padding: 0;

      &:before {
        content: "";
        margin-left: 8px;
      }

      strong {
        &:after {
          content: "";
          margin-right: 16px;
        }
      }
    }
  }
}



// ELEMENTS LIST BOX LTR
// ---------------------------------------------------------------------------------------------------------------------

.bjb-listbox {
  display: inline-block;
  padding: 30px;
  background-color: $bjb-white;

  .row {
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }

    &.header {
      @extend .border-bottom;
      padding-bottom: 10px;
      margin: 0 0 15px 0;

      .column-title {
        margin: 0;
        padding: 0;

        h3 {
          line-height: 1;
          text-transform: uppercase;
          margin: 0;
          padding: 0;
        }
      }

      .column-link {
        margin: 0;
        padding: 0;
        text-align: right;

        a {
          font-family: $bjb-font-light;
          font-weight: normal;
          font-size: $font-size-12;
          line-height: 1;
          color: $bjb-blue;
          text-transform: none;
          padding: 0;
        }
      }
    }

    .column-image {
      img {
        width: 100%;
      }
    }

    .column-text {
      padding-top: 5px;

      h5 {
        color: $bjb-blue;
        text-transform: uppercase;
      }

      p {
        font-family: $bjb-font-light;
        font-size: $font-size-14;
        color: $bjb-black;
        margin-bottom: 0;

        a {
          display: inline;
          font-family: $bjb-font-light;
          font-size: $font-size-14;
          color: $bjb-blue;
        }
      }

      a {
        margin-top: 1rem;
        display: inline-block;
        font-size: $font-size-13;
        color: $bjb-blue;
      }
    }
  }
}



// NEWS LIST WITH TABS
// ---------------------------------------------------------------------------------------------------------------------

.bjb-newslist {
  >ul {
    margin-bottom: 15px;
    border-bottom: 1px solid $bjb-blue-40 !important;

    li {
      margin-right: 3px;

      a {
        padding: 8px 30px 7px 30px;
        border-radius: 0 !important;
        font-family: $bjb-font-light;
        font-size: $font-size-13;
        line-height: 1;
        text-decoration: none !important;
        color: $bjb-blue !important;
        border: 1px solid $bjb-blue-40 !important;
        border-bottom: none !important;

        &:hover,
        &.active {
          background-color: $bjb-blue !important;
          color: $bjb-white !important;
          border: 1px solid $bjb-blue !important;
        }
      }
    }
  }

  >div {
    h5 {
      color: $bjb-blue;
      line-height: 1;
      margin: 0 0 6px 6px;
    }

    ul {
      @extend .border-top;
      margin: 0;
      padding: 0;
      list-style: none;

      li {
        position: relative;
        font-family: $bjb-font-light;
        font-size: $font-size-12;
        color: $bjb-black;
        border-color: $bjb-black-20;
        margin: 0;
        padding: 6px 0;

        &:hover {
          background-color: $bjb-stone-10;
        }

        &:before {
          content: "";
          margin-left: 6px;
        }

        &:after {
          position: absolute;
          display: block;
          content: "";
          width: 100%;
          height: 1px;
          background-color: $bjb-black-20;
          margin-top: 6px;
        }

        span {
          min-width: 100px;
          display: inline-block;

          &:after {
            content: "";
            margin-right: 12px;
          }
        }
      }
    }
  }
}



// ELEMENTS INTERACTIVE LIST
// ---------------------------------------------------------------------------------------------------------------------

.bjb-interactive-list {
  background-color: $bjb-white;
  padding: 30px;

  table {
    td {
      vertical-align: top;
    }
  }

  table {
    &.dataTable {
      thead {
        th,
        td {
          font-family: $bjb-font-light;
          font-size: $font-size-13;
          line-height: 1;
          color: $bjb-blue;
          padding: 5px 16px 5px 8px;
          border-bottom: 1px solid #111;

          &.sorting,
          &.sorting_asc,
          &.sorting_desc {
            background-image: url('../img/icons/icon_sort.svg');
            background-size: 15px 11px;
            background-position: right 8px center;
          }
        }
      }

      tbody {
        tr {
          &:hover {
            background-color: $bjb-stone-10;
          }

          td {
            padding: 5px 8px;

            a {
              font-size: 12px;
            }
          }
        }
      }

      &.no-footer {
        border-bottom: none;
      }
    }
  }
}


// DESCRIPTIONS
// ---------------------------------------------------------------------------------------------------------------------
dt {
  font-weight: normal;
  font-family: $bjb-font-bold;
}



// OVERVIEW LISTS
// ---------------------------------------------------------------------------------------------------------------------

.bjb-overview-list {
  @extend .bjb-interactive-list;

  .dataTables_wrapper {
    .dataTables_length {
      label {
        font-family: $bjb-font-light;
        font-size: $font-size-12;
        color: $bjb-black;
        margin-bottom: 30px;

        select {
          min-width: 50px;
          margin: 0 4px;
          padding: 2px 16px 2px 4px;
          font-family: $bjb-font-light;
          font-size: $font-size-12;
          color: $bjb-black;
          border: 1px solid $bjb-black;
          border-radius: 0;
          -webkit-appearance: none;
          background-color: white;
          background-image: url(../img/icons/icon_select_arrow.svg);
          background-position: right 4px center;
          background-size: 9px 5px;
          background-repeat: no-repeat;
        }
      }
    }

    .dataTables_filter {
      label {
        font-family: $bjb-font-light;
        font-size: $font-size-12;
        color: $bjb-black;
        margin-bottom: 30px;

        input {
          margin: 0 0 0 4px;
          padding: 2px 4px 2px 4px;
          font-family: $bjb-font-light;
          font-size: $font-size-12;
          color: $bjb-black;
          border: 1px solid $bjb-black;
          border-radius: 0;
        }
      }
    }

    .dataTables_info {
      font-family: $bjb-font-light;
      font-size: $font-size-12;
      color: $bjb-black;
      padding-top: 17px;
    }

    .dataTables_paginate {
      padding-top: 15px;

      .paginate_button {
        font-size: $font-size-12;
        color: $bjb-black-20;

        &.previous,
        &.next {
          margin: 0 1px;
          padding: 2px 8px;
          color: $bjb-blue !important;
          border: 0;
          border-radius: 0;
          background: none;
          background-image: none;
          min-width: 1px;

          &:not(.disabled) {
            &:hover {
              color: $bjb-white !important;
              background-color: $bjb-blue;
            }
          }

          &.disabled {
            color: $bjb-black-20 !important;
          }
        }
      }

      span {
        font-size: $font-size-12;
        color: $bjb-black-20;

        .paginate_button {
          color: $bjb-blue !important;
          margin: 0 1px;
          padding: 2px 8px;
          border: 0;
          border-radius: 0;

          &.current {
            margin: 0 1px;
            padding: 2px 8px;
            border: 0;
            border-radius: 0;
            color: $bjb-white !important;
            background: none;
            background-image: none;
            background-color: $bjb-blue;
            min-width: 1px;
          }

          &:hover {
            margin: 0 1px;
            padding: 2px 8px;
            border: 0;
            border-radius: 0;
            color: $bjb-white !important;
            background: none;
            background-image: none;
            background-color: $bjb-blue;
            min-width: 1px;
          }
        }
      }
    }

  }
}



// LAYOUT BOXES
// ---------------------------------------------------------------------------------------------------------------------

