figure {
  font-size: $font-size-13;
}



// FORM
// ---------------------------------------------------------------------------------------------------------------------

.bjb-form {
  margin: 0 auto;
  background-color: white;
  padding: 45px;
  max-width: 600px;

  &--inverted {
    background-color: $bjb-blue;
  }

  h3 {
    font-family: $bjb-font-light;
    margin-bottom: 30px;
    text-align: center;
  }

  .bjb-divider-blue,
  .bjb-divider-black {
    margin: 50px 0 25px 0;
  }

  .form-control,
  .custom-select {

    &:focus {
      border-color: $bjb-blue;
      box-shadow: 0 0 0 0.1rem rgba(0, 20, 137, 0.1);
    }

    &:disabled {
      background-color: $bjb-white;
      color: $bjb-black-20;
      opacity: 0.85;
    }
  }

  .form-group {
    input {
      font-size: $font-size-12;
      padding: 6px;
      border-radius: 0;

      &::placeholder {
        color: $bjb-black-20;
      }

      &[type=text],
      &[type=password] {
        &.success {
          border-color: $bjb-forest-green;
          background-image: url('../img/icons/icon_success.svg');
          background-size: 16px 16px;
          background-repeat: no-repeat;
          background-position: right 8px center;
          padding-right: 32px;

          &:focus {
            box-shadow: none;
          }
        }

        &.warning {
          border-color: $bjb-amber-yellow;
          background-image: url('../img/icons/icon_warning.svg');
          background-size: 16px 16px;
          background-repeat: no-repeat;
          background-position: right 8px center;
          padding-right: 32px;

          &:focus {
            box-shadow: none;
          }
        }

        &.error {
          border-color: $bjb-camine-red;
          background-image: url('../img/icons/icon_error.svg');
          background-size: 16px 16px;
          background-repeat: no-repeat;
          background-position: right 8px center;
          padding-right: 32px;

          &:focus {
            box-shadow: none;
          }
        }
      }
    }

    select {
      font-size: $font-size-12;
      border-radius: 0;
      height: 2rem;
      background-image: url('../img/icons/icon_select_arrow.svg');
      background-size: 9px 5px;
      background-repeat: no-repeat;

      option:first-child {
        color: $bjb-black-20;
      }

      &:disabled {
        background-image: url('../img/icons/icon_select_arrow_disabled.svg');
      }
    }

    label {
      font-size: $font-size-13;
      margin-bottom: 5px;
      white-space: nowrap;

      &.disabled {
        color: $bjb-black-20;
        opacity: 0.85;
      }

      &.success {
        color: $bjb-forest-green;
      }

      &.warning {
        color: $bjb-amber-yellow;
      }

      &.error {
        color: $bjb-camine-red;
      }
    }

    &.row {
      margin-bottom: 7px;

      .form-check {
        margin-top: 0rem;
        margin-bottom: 0.5rem;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .form-check {
    margin-bottom: 1.25rem;
    margin-top: -0.5rem;
    padding-left: 15px;

    .form-check-input:disabled {
      color: $bjb-black-20;
      opacity: 0.35;

      &~.form-check-label {
        color: $bjb-black-20;
        opacity: 0.35;
      }
    }

    input {
      margin-top: 0.25rem;
    }

    label {
      font-family: $bjb-font-light;
      font-size: $font-size-12;
      white-space: nowrap;
    }

    input[type=checkbox] {
      display: none;

      &~label {
        &:before {
          position: absolute;
          width: 10px;
          height: 10px;
          content: "";
          background-image: url('../img/icons/icon_checkbox.svg');
          background-repeat: no-repeat;
          margin-left: -15px;
          margin-top: 4px;
        }
      }

      &:checked {
        &~label {
          &:before {
            background-image: url('../img/icons/icon_checkbox_checked.svg');
          }
        }
      }
    }

    input[type=radio] {
      display: none;

      &~label {
        &:before {
          position: absolute;
          width: 50px;
          height: 50px;
          content: "";
          background-image: url('../img/icons/icon_radio.svg');
          background-repeat: no-repeat;
          margin-left: -16px;
          margin-top: 3px;
          background-size: 25%;
        }
      }

      &:checked {
        &~label {
          &:before {
            background-image: url('../img/icons/icon_radio_checked.svg');
          }
        }
      }
    }
  }

  a:not(.btn),
  a:not(.btn):link,
  a:not(.btn):active,
  a:not(.btn):visited {
    font-size: $font-size-12;
  }
}



.bjb-shadow {
  box-shadow: 0 30px 20px -25px #bbb;
}



// SUBNAVIGATION
// ---------------------------------------------------------------------------------------------------------------------

.bjb-subnavigation {
  h3 {
    font-size: 12px;
    padding-left: 10px;
  }

  ul {
    padding-left: 10px;
    list-style: none;

    li {
      a {
        font-size: $bjb-font-size-small;
        line-height: 141%;
      }
    }
  }
}



// SEVERAL TABS
// ---------------------------------------------------------------------------------------------------------------------

.bjb-nav-multi-level {
  @extend .bjb-newslist;

  ul {
    li {
      min-width: 100px;
      text-align: center;

      a {
        padding: 8px 8px 7px 8px !important;
      }
    }

    &:first-child {
      margin-bottom: 0;
    }

    &:not(:first-child) {
      background-color: $bjb-white;

      li {
        margin-bottom: 0;
        display: inline-block;
        position: relative;

        a {
          border: 1px solid transparent !important;
          margin: 4px 0 0 4px !important;
          color: $bjb-blue !important;

          &:hover,
          &.active {
            background-color: $bjb-white !important;
            color: $bjb-blue !important;
            text-decoration: none !important;
          }
        }

        &.active,
        &:hover {
          &:after {
            content: '';
            position: absolute;
            display: block;
            bottom: -2px;
            width: 100%;
            height: 3px;
            background-color: $bjb-blue;
          }

          a {
            font-weight: bold;
          }
        }
      }
    }
  }
}

.bjb-nav-multi-level + .bjb-interactive-list {
  padding: 0;

  th {
    font-weight: normal;
  }
}



// FOOTER
// ---------------------------------------------------------------------------------------------------------------------

.bjb-footer {
  width: 100%;
  background-color: $bjb-white;
  padding: 30px 0 20px 0;
  z-index: 10;

  .row {
    &:first-child {
      .icon {
        position: absolute;
        top: -5px;
        right: 15px;
      }

      h5 {
