    }
    th {
      border-radius: 0;

      &.dow {
        color: $bjb-black-20;
      }
    }
    td {
      border-radius: 0;

      &.day,
      &.month,
      &.year {
        &.active:hover {
          background-color: $bjb-blue;
        }
        &:hover {
          background-color: $bjb-stone-40;
        }
      }
      &.old,
      &.new {
