      }
      &.active {
        background-color: $bjb-stone;
      }
      &.today:before {
        border-bottom-color: $bjb-stone;
      }
      span {
        &.active {
          background-color: $bjb-blue;
          &:hover {
            background-color: $bjb-stone-40;
          }
        }
        &:hover {
          background-color: $bjb-stone-40;
        }
      }
    }
  }
}

// SWITCHER
// ---------------------------------------------------------------------------------------------------------------------
.bjb-switch {
  .custom-control-label {
    padding-top: 4px;
  }

  .custom-control-input {
    outline: none;
  }

  .custom-control-label,
  .custom-control-label::before,
  .custom-control-label::after {
    cursor: pointer;
  }

  .custom-control-input:focus ~ .custom-control-label::before {
    box-shadow: none;
  }

  .custom-control-input:checked ~ .custom-control-label::before {
    border-color: $bjb-blue;
    background-color: $bjb-blue;
  }

  &.bjb-switch-medium {
    min-height: 2.5rem;

    .custom-control-input:checked ~ .custom-control-label::after {
      transform: translateX(1.5rem);
    }

    .custom-control-label {
      padding-top: 0.75rem;
      left: 2rem;

      &::before {
        left: -4.25rem;
        width: 3.5rem;
        height: 2rem;
        border-radius: 1rem;
      }

      &::after {
        left: calc(-4.25rem + 2px);
        width: 1.75rem;
        height: 1.75rem;
        border-radius: 1rem;
      }
    }
  }

  &.bjb-switch-large {
    min-height: 3.5rem;

    .custom-control-input:checked ~ .custom-control-label::after {
      transform: translateX(2rem);
    }

    .custom-control-label {
      padding-top: 1.25rem;
      left: 3.5rem;

      &::before {
        left: -5.75rem;
        width: 5rem;
        height: 3rem;
        border-radius: 2rem;
      }

      &::after {
        left: calc(-5.75rem + 2px);
        width: 2.75rem;
        height: 2.75rem;
        border-radius: 2rem;
      }
    }
  }
}

