// BJB-LOADING-ICON
// ---------------------------------------------------------------------------------------------------------------------

//.bjb-loading-icon {
//  width: 100px;
//  text-align: center;
//}
//
//.bjb-loading-icon-big {
//  width: 250px;
//  text-align: center
//}

.bjb-loading-icon > div {
  width: 24px;
  height: 24px;
  background-color: #001489;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both
}

.bjb-loading-icon.bjb-loading-icon-small > div {
  width: 12px;
  height: 12px;
}

.bjb-loading-icon.bjb-loading-icon-big > div {
  width: 36px;
  height: 36px;
}

.bjb-loading-icon .bjb-loading-icon-circle-1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s
}

.bjb-loading-icon .bjb-loading-icon-circle-2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s
}

@-webkit-keyframes sk-bouncedelay {
  0%,80%,100% {
    -webkit-transform: scale(0)
  }

  40% {
    -webkit-transform: scale(1)
  }
}

@keyframes sk-bouncedelay {
  0%,80%,100% {
    -webkit-transform: scale(0);
    transform: scale(0)
  }

  40% {
    -webkit-transform: scale(1);
    transform: scale(1)
  }
}
