
@media only screen and (max-width: 1440px) {

  .bjb-header {
    &.bjb-header-rectangle-logo {

      img {
        width: 250px;
        padding: 30px 30px 25px 30px;
        margin-top: -46px;
        margin-left: -128px;
      }
    }
  }

}

@media only screen and (max-width: 1025px) {
  .bjb-listbox {
    display: block;
    max-width: 600px;

    .column-image,
    .column-text {
      width: 100%;
      max-width: 100%;
      flex-basis: 100%;
    }
  }

  .bjb-header {
    &.bjb-header-rectangle-logo {
      img {
        width: 200px;
        padding: 25px 25px 20px 25px;
        margin-top: -37px;
        margin-left: -100px;
      }
    }
  }

}


