  // ---------------------------------------------------------------------------------------------------------------------
  // BJB HEADER
  // ---------------------------------------------------------------------------------------------------------------------
  .bjb-header {
    .bjb-header-lang {
      ul.desktop {
        display: none !important;
      }

      ul.mobile {
        display: block !important;
        padding: 0 !important;
        max-width: 140px;
        background: transparent;

        &.multi-navigation {

          li {
            padding: 5px 0 20px 5px !important;
          }

          .dropdown-menu {
              //display: none !important;
          }
        }

        li {
          padding-top: 5px;
          padding-left: 5px;
          margin-left: 0;
          /* max-width: 100%; */
          margin: 0 !important;
          text-align: left;

          a {
            text-align: left;
            color: $bjb-blue !important;
          }


          .dropdown-menu {
            min-width: initial;
            max-width: 140px;
            top: 10px;
            border: 1px solid #ccc;
          }
        }
      }

      .bjb-navigation-lang {
        margin-top: 0 !important;
        min-width: initial;
        max-width: 140px;
        margin: 0;
        background: transparent;
        z-index: 101;

        > div {
          min-width: initial;
          max-width: 140px;
          margin: 0;
        }
      }



      .bjb-header-util {
        padding-right: 35px;
      }
    }

    &.bjb-header-rectangle-logo {
      height: 250px;
      // max-height: initial;

      img {
        width: 150px;
        transform: translate3d(calc(-50% - 25px),calc(-50% - 25px),0);
        margin: 25px 25px 20px 25px;
      }

      &.bjb-header-rectangle-logo-higher {
        height: 375px;
      }
    }

  }



  .bjb-help {
    display: none !important;
  }


  .bjb-navigation {
    line-height: 30px;
  }


  .bjb-header-util {
    margin-top: 25px !important;
    padding-right: 15px;
  }


  .bjb-header:not(.fix) nav .dropdown .dropdown-toggle::after, .bjb-header-hidden-nav:not(.fix) nav .dropdown .dropdown-toggle::after {
    margin-top: 4px !important;
  }

