  // ---------------------------------------------------------------------------------------------------------------------
  // COLORS
  // ---------------------------------------------------------------------------------------------------------------------
  .font-colour-palette,
  .colour-palette {
    width: 100%;
    float: left;
    max-width: 20%;
    padding-right: 12px;
    min-width: 115px;
    margin: 15px 0;

    strong,
    p {
      font-size: 11px;
      line-height: 120%;
    }
  }



  // ---------------------------------------------------------------------------------------------------------------------
  // EXAMPLE PAGE
  // ---------------------------------------------------------------------------------------------------------------------
  .example-page {
    .col {
      flex-basis: 100%;
    }

    a {
      width: 100%;
    }
  }



  // ---------------------------------------------------------------------------------------------------------------------
  // FOOTER
  // ---------------------------------------------------------------------------------------------------------------------
  .bjb-footer {
    padding: 30px 15px 20px 15px;

    .row {
      &:first-child {
        .icon {
          position: absolute;
          bottom: 40px;
          top: auto;
          right: 15px;
        }

        h5 {
          margin-bottom: 10px;
        }
      }

      &:last-child {
        p {
          text-align: left;
          margin-top: 20px;
        }
      }

      .col {
        flex-basis: 100%;
        padding-bottom: 30px;


        &:last-child {
          padding-bottom: 0;
        }
      }

      .divider {
        margin: 0px 15px 30px 15px;
      }

      .bjb-link-arrow {
        display: block;
        padding-bottom: 5px;
      }

    }
  }
