
.tooltip-custom {
  background-color: #001489;
  color: white;

  min-width: 316px;
  box-sizing: border-box;
  border: 1px solid #001489;

  box-shadow: 0px 14px 14px -6px rgba(0, 0, 0, 0.15);
}

.tooltip-custom .title {
  padding: 5px 8px 5px;
}

.tooltip-custom .tooltip-inner {
  padding-top: 10px;
}

.tooltip-custom .tooltip-inner p {
  margin-top: 0.3rem;
  margin-bottom: 0.85em;
}

.tooltip-custom.show {
  opacity: 1;
}

.tooltip-custom {
  padding: 0;
}

.tooltip-custom .tooltip-inner {
  background-color: white;
  color: black;

  border-radius: 0;
  width: 100%;
  min-width: 100%;
}

.tooltip-custom .arrow {
  /* arrow according to 'Popper' docs */
  border: 1px solid #001489 !important;
  background-color: white;

  height: 10px;
  width: 10px;

  z-index: -1 !important;
  transform: rotate(45deg);

}

.tooltip-custom .arrow::before {
  /* remove arrow from Bootstrap */
  border: none !important;
  /*border-top-color: white !important;*/
}

.tooltip-custom .arrow::before {
  background-color: red;
}

.bs-tooltip-top {
  margin-bottom: 6px;
}

.tooltip-custom.bs-tooltip-top .arrow {
  margin-bottom: -5px;
}

.tooltip-custom.bs-tooltip-right {
  margin-left: 8px;
}

.tooltip-custom.bs-tooltip-right .arrow {
  margin-left: -5px !important;
}

.tooltip-custom.bs-tooltip-bottom {
  margin-top: 8px;
}

.tooltip-custom.bs-tooltip-bottom .arrow {
  margin-top: -5px !important;
  background-color: #001489;
  border: none;
}

.bs-tooltip-top .arrow::before {

}


@media screen and (max-width: 500px) {
  .tooltip-custom {
    min-width: calc(100vw - 16px);
    max-width: calc(100vw - 16px);
    margin-left: 3px;

    font-size: 14px;

    /* NOTICE: This is necessary for functionality on mobile devices */
    pointer-events: none;
    /* ****** */
  }

  .tooltip-custom .arrow {
    display: none !important;
  }

  .bs-tooltip-right .arrow {
    display: none;
  }
}
